import React from 'react';
import styled from 'styled-components';
import { Carousel } from 'antd';

const Wrapper = styled.div`
	height: calc(100vh - 120px);
	max-height: 650px;
	background: url('https://images.unsplash.com/photo-1500932334442-8761ee4810a7?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80');
	background-position-x: 50%;
	background-repeat: no-repeat;
	background-size: cover;
	display: flex;
	align-items: center;
`;

const Container = styled.div`
	max-width: 880px;
	margin: auto;
`;

const Heading = styled.h1`
	color: white;
	font-size: 48px;
	line-height: 52px;
`;

const Sub = styled.p`
	color: white;
	font-size: 34px;
	line-height: 40px;
`;

const contentStyle = {
	height: '160px',
	color: '#fff',
	lineHeight: '160px',
	textAlign: 'center',
	background: '#364d79',
};

const HomePageBanner = () => (
	<Carousel autoplay>
		<div>
			<Wrapper>
				<Container>
					<Heading>
						Connect with experts to get the job done on Airtasker
					</Heading>
					<Sub>It’s amazing what you can’t do yourself</Sub>
				</Container>
			</Wrapper>
		</div>
		<div>
			<Wrapper>
				<Container>
					<Heading>
						Connect with experts to get the job done on Airtasker
					</Heading>
					<Sub>It’s amazing what you can’t do yourself</Sub>
				</Container>
			</Wrapper>
		</div>
		<div>
			<Wrapper>
				<Container>
					<Heading>
						Connect with experts to get the job done on Airtasker
					</Heading>
					<Sub>It’s amazing what you can’t do yourself</Sub>
				</Container>
			</Wrapper>
		</div>
		<div>
			<Wrapper>
				<Container>
					<Heading>
						Connect with experts to get the job done on Airtasker
					</Heading>
					<Sub>It’s amazing what you can’t do yourself</Sub>
				</Container>
			</Wrapper>
		</div>
	</Carousel>
);

export default HomePageBanner;
