import React from 'react';
import styled from 'styled-components';
import signUpUser from '../../../apis/signUpUser';
import formObj from '../../../utils/formObj';
import Input from '../../../components/Input';
import FormItem from '../../../components/FormItem';
import InputErrorMsg from '../../../components/InputErrorMsg';
import ButtonContinue from '../../../components/ButtonContinue';
import FormTitle from '../../../components/FormTitle';
import { Select } from 'antd';

const FormWrapper = styled.form`
	display: flex;
	flex-direction: column;
	font-size: 15px;
`;

class FormGolfDetails extends React.Component {
	constructor(props) {
		super(props);
	}

	back = (event) => {
		event.preventDefault();
		this.props.handlePreStep();
	};

	handleSubmit(e, data, hasError) {
		e.preventDefault();
		console.log(data, hasError);
		this.props.handleContinue();
		const newUser = formObj(data);
		console.log(newUser);
		// const user = {
		// 	id: 5,
		// 	firstName: 'test4',
		// 	lastName: 'test4',
		// 	email: 'admin@gmail.com',
		// 	password: 'aaaaaa',
		// };
		try {
			signUpUser(newUser).then((data) => {
				this.props.handleNextStep();
			});
		} catch (error) {
			console.log(error);
		}
	}

	render() {
		const {
			data,
			handleDataChange,
			handleBlurredChange,
			handleSelectChange,
			getError,
			getErrorMessage,
		} = this.props;
		const { Option } = Select;
		const error = getError(data);
		const hasError = Object.keys(error).length > 0;
		return (
			<>
				<FormTitle variant="primary">Registration</FormTitle>

				<FormWrapper>
					<FormItem label="Golf Link Number" htmlFor="golfLinkNumber">
						<Input
							size="smLeft"
							name="golfLinkNumber"
							id="golfLinkNumber"
							type="text"
							value={data.golfLinkNumber.value}
							onChange={handleDataChange}
							onBlur={handleBlurredChange}
							error={getErrorMessage(error, 'golfLinkNumber')}
						/>

						<InputErrorMsg>
							{getErrorMessage(error, 'golfLinkNumber')}
						</InputErrorMsg>
					</FormItem>
					<FormItem label="Grade" htmlFor="grade">
						<Select
							defaultValue={data.grade.value}
							style={{ width: 120 }}
							onChange={handleSelectChange}
						>
							<Option value="A">A</Option>
							<Option value="B">B</Option>
							<Option value="C">C</Option>
						</Select>
					</FormItem>

					{/* <FormItem label="Last Name" htmlFor="lastName">
							<Input
								size="smRight"
								name="lastName"
								id="lastName"
								type="text"
								value={data.lastName.value}
								onChange={this.handleDataChange}
								onBlur={this.handleBlurredChange}
								error={this.getErrorMessage(error, 'lastName')}
							/>
							<InputErrorMsg>
								{this.getErrorMessage(error, 'lastName')}
							</InputErrorMsg>
						</FormItem> */}

					<FormItem label="Handcap" htmlFor="handicap">
						<Input
							size="smLeft"
							name="handicap"
							id="handicap"
							type="text"
							value={data.handicap.value}
							onChange={handleDataChange}
							onBlur={handleBlurredChange}
							error={getErrorMessage(error, 'handicap')}
						/>
						<InputErrorMsg>{getErrorMessage(error, 'handicap')}</InputErrorMsg>
					</FormItem>

					{/* <RadioWrapper>
						<Radio.Group
							name="genderChecked"
							onChange={this.handleDataChange}
							value={data.genderChecked.value}
						>
							<Radio value={1}>Male</Radio>
							<Radio value={2}>Female</Radio>
							<Radio value={3}>Other</Radio>
						</Radio.Group>
					</RadioWrapper>

					<FormItem
						label="Email"
						info="by entering your email address, you agree to receive invoice, newletter and other notifications from WACT4BBB"
						htmlFor="email"
					>
						<Input
							size="lg"
							name="email"
							id="email"
							type="email"
							value={data.email.value}
							onChange={this.handleDataChange}
							onBlur={this.handleBlurredChange}
							error={this.getErrorMessage(error, 'email')}
						/>
						<InputErrorMsg>
							{this.getErrorMessage(error, 'email')}
						</InputErrorMsg>
					</FormItem>
					<FormItem label="Phone number" htmlFor="phone">
						<Input
							size="lg"
							name="phone"
							id="phone"
							type="text"
							value={data.phone.value}
							onChange={this.handleDataChange}
							onBlur={this.handleBlurredChange}
							error={this.getErrorMessage(error, 'phone')}
						/>
						<InputErrorMsg>
							{this.getErrorMessage(error, 'phone')}
						</InputErrorMsg>
					</FormItem>
					<FormItem
						label="Password"
						htmlFor="password"
						info="Your password need to be at least 6 characters with at least one Captain letter and at least one Number"
					>
						<Input
							size="lg"
							name="password"
							id="password"
							type="password"
							value={data.password.value}
							onChange={this.handleDataChange}
							onBlur={this.handleBlurredChange}
							error={this.getErrorMessage(error, 'password')}
						/>
						<InputErrorMsg>
							{this.getErrorMessage(error, 'password')}
						</InputErrorMsg>
						<InputErrorMsg>
							{this.comparePassword(error, 'PasswordNotMatch')}
						</InputErrorMsg>
					</FormItem>
					<FormItem label="Confirm Password" htmlFor="confirmPassword">
						<Input
							size="lg"
							name="confirmPassword"
							id="confirmPassword"
							type="password"
							value={data.confirmPassword.value}
							onChange={this.handleDataChange}
							onBlur={this.handleBlurredChange}
							error={this.getErrorMessage(error, 'confirmPassword')}
						/>
						<InputErrorMsg>
							{this.getErrorMessage(error, 'confirmPassword')}
						</InputErrorMsg>
						<InputErrorMsg>
							{this.comparePassword(error, 'PasswordNotMatch')}
						</InputErrorMsg>
					</FormItem>
					<CheckAgebox>
						<label htmlFor="ageChecked">
							<input
								name="ageChecked"
								type="checkbox"
								id="ageChecked"
								checked={data.ageChecked.value}
								onChange={this.handleDataChange}
							/>
							{`By clicking this box, I confirm I am over 18 years old`}
						</label>
						<InputErrorMsg>
							{this.getErrorMessage(error, 'ageChecked')}
						</InputErrorMsg>
					</CheckAgebox>
					<Checkbox>
						<label htmlFor="towelChecked">
							<input
								name="towelChecked"
								type="checkbox"
								id="towelChecked"
								checked={data.towelChecked.value}
								onChange={this.handleDataChange}
							/>
							{`By signing up, I agree to WACT4BBB's  `}
						</label>
						<LinkItem href="https://www.w3schools.com">
							{`Terms & Conditions & Privacy Policy.`}
						</LinkItem>

						<InputErrorMsg>
							{this.getErrorMessage(error, 'towelChecked')}
						</InputErrorMsg>
					</Checkbox>
					{isSubmitFail && <ServerMsg status="error">{submitError}</ServerMsg>} */}
					<ButtonContinue onClick={this.back}> PRE</ButtonContinue>
					<ButtonContinue
						onClick={(e) => {
							this.handleSubmit(e, data, hasError);
						}}
					>
						{' '}
						SUBMIT
					</ButtonContinue>
				</FormWrapper>
			</>
		);
	}
}

export default FormGolfDetails;
