import styled, { css } from 'styled-components';

const Input = styled.input`
	border: none;
	border-radius: 3px;
	background-color: rgba(196, 196, 196, 0.23);
	height: 30px;
	padding: 0 15px;
	:hover {
		border-color: rgb(0, 143, 180);
		border-width: 1px;
		border-style: solid;
	}
	:focus {
		outline: none !important;
		border-color: rgb(0, 143, 180);
		border-width: 1px;
		border-style: solid;
	}

	${(props) =>
		({
			smLeft: css`
				width: 220px;
				margin-right: 10px;
			`,
			smRight: css`
				width: 220px;
			`,
			lg: css`
				width: 460px;
			`,
		}[props.size])}

	${(props) =>
		props.error &&
		css`
			background-color: rgba(255, 0, 0, 0.05);
		`}
`;

export default Input;
