const formObj = (input) => {
	let newObj = {};
	const array = [
		'firstName',
		'lastName',
		'email',
		'phone',
		'password',
		'golfLinkNumber',
		'grade',
		'handicap',
		'genderChecked',
	];
	for (var i in input) {
		if (array.indexOf(i) !== -1) {
			newObj = {
				...newObj,
				[i]: input[i].value,
			};
		}
	}
	return newObj;
};

export default formObj;
