import React from 'react';
import styled from 'styled-components';
import LogoImg from '../../../assets/images/logo.png';
import PhoneImg from '../../../assets/images/phone.svg';
import EmailImg from '../../../assets/images/email.svg';
import FacebookImg from '../../../assets/images/facebook.svg';
import InsImg from '../../../assets/images/ins.png';
import SendImg from '../../../assets/images/send.svg';
import Input from '../../Input';
import Button from '../../Button';

const Footer = styled.div`
	background: #2c2c2c;
	color: #d3d3d3;
	position: relative;
	padding: 20px 20px 0 20px;
`;

const FooterBottom = styled.div`
	background: #343a40;
	color: #686868;
	height: 50px;
	text-align: center;
	position: absolute;
	bottom: 0px;
	left: 0px;
	padding-top: 20px;
	width: 100%;
`;

const FooterContent = styled.div`
	height: 450px;
	display: flex;
	justify-content: space-between;
`;

const FooterOne = styled.div`
	display: flex;
	flex-direction: column;
	width: 30%;
`;

const Logo = styled.img`
	width: 200px;
	height: 100px;
`;

const About = styled.p`
	font-size: 14px;
	margin-top: 25px;
`;

const FooterContactImg = styled.img`
	width: 20px;
	height: 20px;
	margin-right: 5px;
`;
const FooterContactWrap = styled.div`
	display: flex;
	margin-bottom: -10px;
`;

const FooterSocialWrap = styled.div`
	display: flex;
	margin-top: 10px;
`;

const FooterTextarea = styled.textarea`
	border: none;
	background: #232224;
	color: grey;
	padding: 5px 15px;
	margin-top: 20px;
	resize: none;
	::-webkit-scrollbar-thumb {
		background-color: #cececebb !important;
		border-radius: 10px !important;
	}
	::-webkit-scrollbar {
		width: 12px;
		background-color: #2c2c2c;
	}
`;

const FooterThree = styled.div`
	display: flex;
	flex-direction: column;
`;

const SendButton = styled(Button)`
	margin-top: 20px;
	width: 100px;
	float: right;
`;

const FooterSubTitle = styled.div`
	font-size: 24px;
	font-weight: bold;
	margin-bottom: 35px;
`;

const LinkItem = styled.a`
	color: white;
	:hover {
		color: 09adbb;
	}
`;
const PageFooter = () => (
	<Footer>
		<FooterContent>
			<FooterOne>
				<Logo src={LogoImg} alt="logo" />
				<About>
					Due to covid forcing Australia to close its borders to overseas
					guests, World Amateur 4BBB Champions Tournament has been forced to
					make some changes to the tournament and venue. Please stay tuned for
					updates to the future competitions.
				</About>
				<FooterContactWrap>
					<FooterContactImg src={PhoneImg} alt="logo" />
					<p>134-463-389</p>
				</FooterContactWrap>
				<FooterContactWrap>
					<FooterContactImg src={EmailImg} alt="logo" />
					<p>info@wact4BBB.com</p>
				</FooterContactWrap>
				<FooterSocialWrap>
					<LinkItem href="https://www.w3schools.com">
						<FooterContactImg src={FacebookImg} alt="logo" />
					</LinkItem>
					<LinkItem href="https://www.w3schools.com">
						<FooterContactImg src={InsImg} alt="logo" />
					</LinkItem>
				</FooterSocialWrap>
			</FooterOne>
			<div>
				<FooterSubTitle>Quick Links</FooterSubTitle>
				<li>
					<LinkItem href="https://www.w3schools.com">Events</LinkItem>
				</li>
				<li>
					<LinkItem href="https://www.w3schools.com">Moments</LinkItem>
				</li>
				<li>
					<LinkItem href="https://www.w3schools.com">
						Terms and Conditions
					</LinkItem>
				</li>
			</div>
			<FooterThree id="contact-us">
				<FooterSubTitle>Contact Us</FooterSubTitle>
				<Input placeholder="Your email address" size="smLeft" />
				<FooterTextarea
					id="w3review"
					name="w3review"
					rows="4"
					cols="50"
					placeholder="Message..."
				></FooterTextarea>
				<SendButton size="sm">
					<FooterContactImg src={SendImg} alt="logo" />
					Send
				</SendButton>
			</FooterThree>
		</FooterContent>
		<FooterBottom>
			&copy; wact4bbb.com | Designed by My IT Solution
		</FooterBottom>
	</Footer>
);

export default PageFooter;
