import React from 'react';
import { withRouter } from 'react-router-dom';
import styled from 'styled-components';
import FormTitle from '../../components/FormTitle';
import FormWrapper from '../../components/FormWrapper';
import Input from '../../components/Input';
import FormItem from '../../components/FormItem';
import validate from '../../utils/validate';
import InputErrorMsg from '../../components/InputErrorMsg';
import Button from '../../components/Button';
import ServerMsg from '../../components/ServerMsg';
import { loginUser } from '../../apis/auth';
import { setToken, setUserEmail } from '../../utils/authentication';

const Wrapper = styled.div`
	width: 100%;
	height: 50rem;
	margin: 0px auto;
	background-color: #fff;
	display: flex;
	flex-direction: column;
`;

const SignUpTitle = styled(FormTitle)`
	margin-top: 30px;
	margin-bottom: 30px;
`;

const SubmitButton = styled(Button)`
	border-radius: 0px;
	padding-left: 10px;
	padding-right: 10px;
`;

class SignIn extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			data: {
				email: {
					value: '',
					blurred: false,
				},
				password: {
					value: '',
					blurred: false,
				},
			},
			isFormSubmit: false,
			error: null,
			isLoading: false,
		};

		this.handleDataChange = this.handleDataChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleBlurredChange = this.handleBlurredChange.bind(this);
		this.handleIsFormSubmitChange = this.handleIsFormSubmitChange.bind(this);
	}

	handleDataChange(event) {
		const { name, value } = event.target;
		this.setData(name, {
			value,
		});
	}

	handleIsFormSubmitChange(newIsFormSubmit) {
		this.setState({
			isFormSubmit: newIsFormSubmit,
		});
	}

	handleSubmit(e, data, hasError) {
		e.preventDefault();
		this.setState({
			isFormSubmit: true,
		});
		console.log(data, hasError);
	}

	handleBlurredChange(event) {
		const { name } = event.target;
		this.setData(name, {
			blurred: true,
		});
	}

	getError() {
		const { data } = this.state;
		const error = {};
		Object.keys(data).forEach((name) => {
			const errorOfName = validate(name, data);
			if (!errorOfName) {
				return;
			}
			error[name] = errorOfName;
		});
		return error;
	}

	getErrorMessage(error, name) {
		const { data, isFormSubmit } = this.state;
		const showInputError = data[name].blurred;
		return (showInputError || isFormSubmit) && error[name];
	}

	setData(name, newData) {
		this.setState((prevState) => ({
			data: {
				...prevState.data,
				[name]: {
					...prevState.data[name],
					...newData,
				},
			},
		}));
	}

	login = () => {
		this.setState({ error: null }, () => {
			const { data: loginData } = this.state;
			loginUser(loginData.email.value, loginData.password.value)
				.then((data) => {
					this.setState({ isLoading: true }, () => {
						setToken(data.token);
						setUserEmail(loginData.email.value);
						const { history } = this.props;
						history.replace({
							pathname: '/',
							email: loginData.email.value,
						});
					});
				})
				.catch((error) => this.setState({ error, isLoading: false }));
		});
	};

	render() {
		const { data, error: authError, isLoading } = this.state;
		const error = this.getError(data);

		return (
			<Wrapper>
				<SignUpTitle variant="primary">Login</SignUpTitle>
				<FormWrapper
					onSubmit={(e) => {
						e.preventDefault();
						this.handleIsFormSubmitChange(true);
					}}
				>
					{/* <Alert message="Error Text" type="error" /> */}
					<FormItem label="Email" htmlFor="email">
						<Input
							size="lg"
							name="email"
							id="email"
							type="email"
							value={data.email.value}
							onChange={this.handleDataChange}
							onBlur={this.handleBlurredChange}
							error={this.getErrorMessage(error, 'email')}
						/>
						<InputErrorMsg>
							{this.getErrorMessage(error, 'email')}
						</InputErrorMsg>
					</FormItem>
					<FormItem label="Password" htmlFor="password">
						<Input
							size="lg"
							name="password"
							id="password"
							type="password"
							value={data.password.value}
							onChange={this.handleDataChange}
							onBlur={this.handleBlurredChange}
							error={this.getErrorMessage(error, 'password')}
						/>
						<InputErrorMsg>
							{this.getErrorMessage(error, 'password')}
						</InputErrorMsg>
					</FormItem>
					<SubmitButton onClick={this.login} size="sm" disabled={isLoading}>
						Login
					</SubmitButton>
					{authError && (
						<ServerMsg status="error">
							Login failed, Please try again.
						</ServerMsg>
					)}
					{isLoading && <ServerMsg status="success">Login Success!</ServerMsg>}
				</FormWrapper>
			</Wrapper>
		);
	}
}

