import React from 'react';
import styled from 'styled-components';
import { withRouter } from 'react-router-dom';
import NakedButton from '../../NakedButton';
import Button from '../../Button';
import LogoImg from '../../../assets/images/logo.png';

const Wrapper = styled.div`
	background-color: #2c2c2c;
`;

const Container = styled.div`
	width: 1000px;
	margin: auto;
	height: 100px;
	display: flex;
	align-items: center;
`;

const Logo = styled.img`
	margin-right: 32px;
	width: 200px;
	height: 100px;
`;

const Item = styled(NakedButton)`
	color: white;
	font-size: 14px;
	padding: 12px 0;
	margin: 0 12px;
	font-weight: bold;
	cursor: pointer;
	border-top: 2px solid transparent;
	border-bottom: 2px solid transparent;

	&:hover {
		color: rgba(255, 255, 255, 0.8);
		border-top: 2px solid white;
	}
`;

const Left = styled.div`
	display: flex;
	align-items: center;
`;

const Right = styled.div`
	display: flex;
	margin-left: auto;
	align-items: center;
`;

const ContactButton = styled(Button)`
	margin: 0 12px;
`;

const PageHeader = ({ history }) => (
	<Wrapper>
		<Container>
			<Left>
				<Logo src={LogoImg} alt="logo" />
			</Left>
			<Right>
				<Item
					as="a"
					onClick={() => {
						history.push('/');
					}}
				>
					Home
				</Item>
				<Item as="a">Tournament</Item>
				<Item as="a">Members</Item>
				<Item
					as={NakedButton}
					onClick={() => {
						history.push('/signup');
					}}
				>
					Sign up
				</Item>
				<Item
					as={NakedButton}
					onClick={() => {
						history.push('/signin');
					}}
				>
					Log in
				</Item>
				<Button variant="transparent" size="sm">
					Become a Captain
				</Button>
				<ContactButton
					onClick={() => {
						let anchorElement = document.getElementById('contact-us');
						if (anchorElement) {
							anchorElement.scrollIntoView();
						}
					}}
					size="sm"
				>
					Contace Us
				</ContactButton>
			</Right>
		</Container>
	</Wrapper>
);

export default withRouter(PageHeader);
