import React from 'react';
import styled from 'styled-components';
import FormUserDetails from './FormUserDetails';
import FormGolfDetails from './FormGolfDetails';
import ProgressionBar from './ProgressionBar';
import validate from './validate';
import ButtonContinue from '../../components/ButtonContinue';
import { getUsers } from '../../apis/getUsers';

const Container = styled.div`
	display: flex;
	flex-direction: column;
	align-items: center;
	min-height: 100vh;
	background-color: #fff;
	padding-bottom: 2rem;
`;

const FormWrapper = styled.div`
	display: flex;
	flex-direction: column;
	font-size: 15px;
`;

const WordWrapper = styled.div`
	width: 280px;
`;

const initialData = {
	value: '',
	blurred: false,
};

class Registration extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			step: 1,
			data: {
				firstName: initialData,
				lastName: initialData,
				email: initialData,
				phone: initialData,
				password: initialData,
				confirmPassword: initialData,
				towelChecked: {
					value: false,
				},
				ageChecked: {
					value: false,
				},
				genderChecked: {
					value: '',
				},
				grade: {
					value: 'A',
				},
				golfLinkNumber: initialData,
				handicap: initialData,
				isExistingEmail: false,
			},
			isFormSubmit: false,
			submitError: '',
		};
		this.handleNextStep = this.handleNextStep.bind(this);
		this.handleDataChange = this.handleDataChange.bind(this);
		this.handleBlurredChange = this.handleBlurredChange.bind(this);
		this.getError = this.getError.bind(this);
		this.getErrorMessage = this.getErrorMessage.bind(this);
		this.comparePassword = this.comparePassword.bind(this);
		this.handlePreStep = this.handlePreStep.bind(this);
		this.handleContinue = this.handleContinue.bind(this);
		this.handleSelectChange = this.handleSelectChange.bind(this);
		this.handleFinishVerification = this.handleFinishVerification.bind(this);
	}

	handleNextStep() {
		const { step } = this.state;
		if (step < 3) this.setState({ step: step + 1 });
	}

	handlePreStep() {
		const { step } = this.state;
		if (step === 2) this.setState({ step: step - 1 });
	}

	handleDataChange(event) {
		const value =
			event.target.type === 'checkbox'
				? event.target.checked
				: event.target.value;
		const { name } = event.target;

		this.setData(name, {
			value,
		});
	}

	handleSelectChange(value) {
		console.log(`selected ${value}`);
		this.setData('grade', {
			value,
		});
	}

	async handleBlurredChange(event) {
		const { name, value } = event.target;
		this.setData(name, {
			blurred: true,
		});

		if (name === 'email') {
			const x = await getUsers();
			console.log('here', x);

			if (!x) {
				x.forEach((element) => {
					console.log(element.email);
					if (value === element.email) {
						console.log('existiong email');
						this.setState((prevState) => ({
							data: {
								...prevState.data,
								isExistingEmail: true,
							},
						}));
					}
				});
			}
		}
		//this.props.handleNextStep();
	}

	setData(name, newData) {
		this.setState((prevState) => ({
			data: {
				...prevState.data,
				[name]: {
					...prevState.data[name],
					...newData,
				},
			},
		}));
	}

	handleContinue() {
		this.setState({
			isFormSubmit: true,
		});
	}

	handleFinishVerification() {
		this.props.history.push('/signin');
	}

	getErrorMessage(error, name) {
		const { data, isFormSubmit } = this.state;
		const showInputError = data[name].blurred;
		return (showInputError || isFormSubmit) && error[name];
	}

	comparePassword(error, name) {
		const { isFormSubmit } = this.state;
		return isFormSubmit && error[name];
	}

	getError() {
		const { data } = this.state;
		const error = {};
		Object.keys(data).forEach((name) => {
			const errorOfName = validate(name, data);
			if (!errorOfName) {
				return;
			}
			error[name] = errorOfName;
		});
		if (data.password.value !== data.confirmPassword.value) {
			if (data.password.value !== '' && data.confirmPassword.value !== '') {
				error['PasswordNotMatch'] = "Passwords didn't match";
			}
		}
		console.log('state', data);
		if (data.isExistingEmail === true) {
			console.log('here');
			error['email'] = 'Email Exist';
		}
		return error;
	}

	render() {
		const { step, data } = this.state;
		return (
			<Container>
				<ProgressionBar step={step} />
				{step === 1 && (
					<FormUserDetails
						data={data}
						handleDataChange={this.handleDataChange}
						handleBlurredChange={this.handleBlurredChange}
						handleNextStep={this.handleNextStep}
						getError={this.getError}
						getErrorMessage={this.getErrorMessage}
						comparePassword={this.comparePassword}
						handleContinue={this.handleContinue}
					/>
				)}
				{step === 2 && (
					<FormGolfDetails
						data={data}
						handleDataChange={this.handleDataChange}
						handleBlurredChange={this.handleBlurredChange}
						handleNextStep={this.handleNextStep}
						handlePreStep={this.handlePreStep}
						handleSelectChange={this.handleSelectChange}
						getError={this.getError}
						getErrorMessage={this.getErrorMessage}
						handleContinue={this.handleContinue}
					/>
				)}
				{step === 3 && (
					<FormWrapper>
						<WordWrapper>
							<p>
								Thanks for registering. Please check you email to verify your
								account. If you finish the verification please click the login
								button
							</p>
						</WordWrapper>

						<ButtonContinue onClick={this.handleFinishVerification}>
							GO TO Login
						</ButtonContinue>
					</FormWrapper>
				)}
			</Container>
		);
	}
}

export default Registration;
