import React from 'react';
import styled, { css } from 'styled-components';

const BREAKPOINT = {
	small: 320,
	medium: 720,
	large: 1024,
};

const Container = styled.div`
	width: 29rem;
	margin: 2rem auto 3rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-family: 'Roboto';

	@media only screen and (max-width: ${BREAKPOINT.medium}px) {
		width: 18rem;
	}

	@media only screen and (max-width: ${BREAKPOINT.small}px) {
		flex-direction: column;
		align-items: flex-start;
		margin-left: 2rem;
		width: auto;
	}
`;

const StageWrapper = styled.div`
	width: max-content;
	position: relative;
`;

const Stage = styled.div`
	width: 4rem;
	height: 4rem;
	line-height: 4rem;
	border: 1px solid #09adbb;
	border-radius: 50%;
	text-align: center;
	transition: all 0.2s;
	&::after {
		content: '';
		display: ${({ children }) => (children === 1 ? 'none' : 'block')};
		background-color: ${({ state, children }) =>
			state >= children ? '#09ADBB' : '#c7c7c7'};
		width: 121px;
		height: 1px;
		position: absolute;
		right: 4.5rem;
		top: 2rem;
		transition: all 0.2s;

		@media only screen and (max-width: ${BREAKPOINT.medium}px) {
			width: 40px;
			right: 4.25rem;
		}

		@media only screen and (max-width: ${BREAKPOINT.small}px) {
			display: none;
		}
	}

	${({ state, children }) =>
		({
			true: css`
				background-color: #09adbb;
				color: #fff;
			`,
			false: css`
				background-color: #fff;
				color: #09adbb;
			`,
		}[state >= children])}
	box-shadow:0 4px 4px 0 rgba(0, 0, 0, 0.1);
`;

const StageDescription = styled.div`
	font-size: 14px;
	white-space: nowrap;
	position: absolute;
	left: 2rem;
	transform: translate(-50%, 8px);

	@media only screen and (max-width: ${BREAKPOINT.small}px) {
		margin-left: 90px;
		margin-top: -50px;
	}
`;

const ProgressionBar = ({ step }) => {
	const stepTable = [
		{ key: 1, stage: 1, description: 'Step1' },
		{ key: 2, stage: 2, description: 'Step2' },
		{ key: 3, stage: 3, description: 'Confirm' },
	];
	return (
		<Container>
			{stepTable.map(({ key, stage, description }) => (
				<StageWrapper key={key}>
					<Stage state={step}>{stage}</Stage>
					<StageDescription>{description}</StageDescription>
				</StageWrapper>
			))}
		</Container>
	);
};

export default ProgressionBar;
