import React from 'react';
import styled from 'styled-components';
import Input from '../../../components/Input';
import FormItem from '../../../components/FormItem';
import InputErrorMsg from '../../../components/InputErrorMsg';
import ButtonContinue from '../../../components/ButtonContinue';
import FormTitle from '../../../components/FormTitle';
import FormWrapper from '../../../components/FormWrapper';
import FlexRow from '../../../components/FlexRow';
import { Radio } from 'antd';
import { getUsers } from '../../../apis/getUsers';

const Checkbox = styled.div`
	font-family: 'Raleway';
	text-align: left;
	margin-left: -65px;
`;

const CheckAgebox = styled.div`
	font-family: 'Raleway';
	text-align: left;
	margin-left: 28px;
`;

const RadioWrapper = styled.div`
	margin-left: -100px;
	margin-bottom: 10px;
`;

const LinkItem = styled.a`
	display: block;
	:hover {
		color: 09adbb;
	}
`;

class FormUserDetails extends React.Component {
	constructor(props) {
		super(props);
		// this.state = {
		// 	isExistingEmail: false,
		// }
	}

	async continue(event, hasError, error, data) {
		event.preventDefault();
		const x = await getUsers();
		console.log(data.email);
		//this.props.handleNextStep();
		x.forEach((element) => {
			console.log(element.email);
			if (data.email.value === element.email) {
				console.log('existiong email');
				this.setState({ isExistingEmail: true });
			}
		});
		console.log(this);
		this.props.handleNextStep();
	}

	render() {
		const {
			data,
			handleDataChange,
			handleBlurredChange,
			getError,
			getErrorMessage,
			comparePassword,
			handleNextStep,
		} = this.props;
		const error = getError(data);
		const hasError = Object.keys(error).length > 0;
		return (
			<>
				<FormTitle variant="primary">Registration</FormTitle>

				<FormWrapper>
					<FlexRow>
						<FormItem label="First Name" htmlFor="firstName">
							<Input
								size="smLeft"
								name="firstName"
								id="firstName"
								type="text"
								value={data.firstName.value}
								onChange={handleDataChange}
								onBlur={handleBlurredChange}
								error={getErrorMessage(error, 'firstName')}
							/>

							<InputErrorMsg>
								{getErrorMessage(error, 'firstName')}
							</InputErrorMsg>
						</FormItem>
						<FormItem label="Last Name" htmlFor="lastName">
							<Input
								size="smRight"
								name="lastName"
								id="lastName"
								type="text"
								value={data.lastName.value}
								onChange={handleDataChange}
								onBlur={handleBlurredChange}
								error={getErrorMessage(error, 'lastName')}
							/>
							<InputErrorMsg>
								{getErrorMessage(error, 'lastName')}
							</InputErrorMsg>
						</FormItem>
					</FlexRow>
					<RadioWrapper>
						<Radio.Group
							name="genderChecked"
							onChange={handleDataChange}
							value={data.genderChecked.value}
						>
							<Radio value={'1'}>Male</Radio>
							<Radio value={'2'}>Female</Radio>
							<Radio value={'3'}>Other</Radio>
						</Radio.Group>
					</RadioWrapper>
					<FlexRow>
						<FormItem
							label="Email"
							info="by entering your email address, you agree to receive invoice, newletter and other notifications from WACT4BBB"
							htmlFor="email"
						>
							<Input
								size="smLeft"
								name="email"
								id="email"
								type="email"
								value={data.email.value}
								onChange={handleDataChange}
								onBlur={handleBlurredChange}
								error={getErrorMessage(error, 'email')}
							/>
							<InputErrorMsg>{getErrorMessage(error, 'email')}</InputErrorMsg>
						</FormItem>
						<FormItem label="Phone number" htmlFor="phone">
							<Input
								size="smRight"
								name="phone"
								id="phone"
								type="text"
								value={data.phone.value}
								onChange={handleDataChange}
								onBlur={handleBlurredChange}
								error={getErrorMessage(error, 'phone')}
							/>
							<InputErrorMsg>{getErrorMessage(error, 'phone')}</InputErrorMsg>
						</FormItem>
					</FlexRow>
					<FlexRow>
						<FormItem
							label="Password"
							htmlFor="password"
							info="Your password need to be at least 6 characters with at least one Captain letter and at least one Number"
						>
							<Input
								size="smLeft"
								name="password"
								id="password"
								type="password"
								value={data.password.value}
								onChange={handleDataChange}
								onBlur={handleBlurredChange}
								error={getErrorMessage(error, 'password')}
							/>
							<InputErrorMsg>
								{getErrorMessage(error, 'password')}
							</InputErrorMsg>
							<InputErrorMsg>
								{comparePassword(error, 'PasswordNotMatch')}
							</InputErrorMsg>
						</FormItem>
						<FormItem label="Confirm Password" htmlFor="confirmPassword">
							<Input
								size="smRight"
								name="confirmPassword"
								id="confirmPassword"
								type="password"
								value={data.confirmPassword.value}
								onChange={handleDataChange}
								onBlur={handleBlurredChange}
								error={getErrorMessage(error, 'confirmPassword')}
							/>
							<InputErrorMsg>
								{getErrorMessage(error, 'confirmPassword')}
							</InputErrorMsg>
							<InputErrorMsg>
								{comparePassword(error, 'PasswordNotMatch')}
							</InputErrorMsg>
						</FormItem>
					</FlexRow>

					<CheckAgebox>
						<label htmlFor="ageChecked">
							<input
								name="ageChecked"
								type="checkbox"
								id="ageChecked"
								checked={data.ageChecked.value}
								onChange={handleDataChange}
							/>
							{`By clicking this box, I confirm I am over 18 years old`}
						</label>
						<InputErrorMsg>
							{getErrorMessage(error, 'ageChecked')}
						</InputErrorMsg>
					</CheckAgebox>
					<Checkbox>
						<label htmlFor="towelChecked">
							<input
								name="towelChecked"
								type="checkbox"
								id="towelChecked"
								checked={data.towelChecked.value}
								onChange={handleDataChange}
							/>
							{`By signing up, I agree to WACT4BBB's  `}
						</label>
						<LinkItem href="https://www.w3schools.com">
							{`Terms & Conditions & Privacy Policy.`}
						</LinkItem>

						<InputErrorMsg>
							{getErrorMessage(error, 'towelChecked')}
						</InputErrorMsg>
					</Checkbox>
					<ButtonContinue
						onClick={(e) => {
							this.continue(e, error, hasError, data);
						}}
					>
						CONTINUE
					</ButtonContinue>
				</FormWrapper>
			</>
		);
	}
}

export default FormUserDetails;
