const validate = (name, data) => {
	const { value } = data[name];
	const NAME_REGEXP = /^[A-Za-z]+$/;
	const EMAIL_REGEXP =
		/^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/;

	switch (name) {
		case 'email': {
			if (!value) {
				return 'Please input your email';
			}
			if (!EMAIL_REGEXP.test(value)) {
				return 'Please input a valid email';
			}
			return '';
		}

		case 'firstName': {
			if (!value) {
				return 'Please input your first name';
			}
			if (!NAME_REGEXP.test(value)) {
				return 'Please input English alphabets only';
			}
			return '';
		}

		case 'lastName': {
			if (!value) {
				return 'Please input your last name';
			}
			if (!NAME_REGEXP.test(value)) {
				return 'Please input English alphabets only';
			}
			return '';
		}

		case 'towelChecked': {
			if (value === false) {
				return 'Please read and tick the box';
			}
			return '';
		}

		case 'password': {
			if (!value) {
				return 'Please enter your password';
			}
			return '';
		}

		case 'confirmPassword': {
			if (!value) {
				return 'Please enter your confirmPassword';
			}
			return '';
		}

		default:
			return '';
	}
};

export default validate;
