import React from 'react';
import styled from 'styled-components';
import { Row, Col } from 'antd';
import LogoImg from '../../../../assets/images/smalltour.jpg';

const Wrapper = styled.div`
	margin-top: 300px;
`;

const Logo = styled.img`
	margin-left: 32px;
	width: 70%;
	height: 70%;
`;

const RightWrapper = styled.div`
	text-align: left;
`;

const MoreDetailWrapper = styled.div`
	display: flex;
	flex-direction: row;
`;

const SpanLine = styled.span`
	background-color: #09adbb;
	height: 1px;
	width: 25px;
	content: '';
	display: block;
	margin-top: 15px;
`;

const SpanDetail = styled.span`
	margin-left: 5px;
	font-size: 20px;
	display: block;
`;

const TitleTour = styled.h3`
	margin-top: 25px;
	font-size: 24px;
`;

const TourDes = styled.p`
	font-size: 18px;
	display: block;
	width: 70%;
`;

const PromoteSection = () => (
	<Wrapper>
		<Row>
			<Col span={12}>
				<Logo src={LogoImg} alt="logo" />
			</Col>
			<Col span={12}>
				<RightWrapper>
					<TitleTour>16 Tournament</TitleTour>
					<TourDes>
						Enjoy Island golf, sea-views from your bed, resort facilities and
						the Great Barrier Reef on your doorstep. Enjoy Island golf,
						sea-views from your bed, resort facilities and the Great Barrier
						Reef on your doorstep.
					</TourDes>
					<MoreDetailWrapper>
						<SpanLine></SpanLine>
						<a href="#">
							<SpanDetail>More Detail</SpanDetail>
						</a>
					</MoreDetailWrapper>
				</RightWrapper>
			</Col>
		</Row>
	</Wrapper>
);

export default PromoteSection;
