import React from 'react';
import styled from 'styled-components';
import { Card } from 'antd';
import CardImg from '../../../../assets/images/card1.png';

const { Meta } = Card;

const Wrapper = styled.div`
	height: calc(50vh - 60px);
	max-height: 300px;
	display: flex;
	flex-direction: column;
	align-items: center;
`;

const Title = styled.h1`
	font-size: 48px;
	line-height: 52px;
	margin-top: 30px;
`;

const CardContainer = styled.div`
	display: flex;
	align-items: center;
	justify-content: space-around;
`;

const CustomizedCard = styled(Card)`
	margin-right: 20px;
	margin-left: 20px;
`;

const Tournament = () => (
	<Wrapper>
		<Title>Choose your tournament</Title>
		<CardContainer>
			<CustomizedCard
				hoverable
				style={{ width: 240 }}
				cover={<img alt="example" src={CardImg} />}
			>
				<Meta
					title="Father & Dad"
					description="2022 Australias offical 4BBB Captains & Teams Cup"
				/>
			</CustomizedCard>
			<CustomizedCard
				hoverable
				style={{ width: 240 }}
				cover={<img alt="example" src={CardImg} />}
			>
				<Meta
					title="Father & Dad"
					description="2022 Australias offical 4BBB Captains & Teams Cup"
				/>
			</CustomizedCard>
			<CustomizedCard
				hoverable
				style={{ width: 240 }}
				cover={<img alt="example" src={CardImg} />}
			>
				<Meta
					title="Father & Dad"
					description="2022 Australias offical 4BBB Captains & Teams Cup"
				/>
			</CustomizedCard>
			<CustomizedCard
				hoverable
				style={{ width: 240 }}
				cover={<img alt="example" src={CardImg} />}
			>
				<Meta
					title="Father & Dad"
					description="2022 Australias offical 4BBB Captains & Teams Cup"
				/>
			</CustomizedCard>
		</CardContainer>
	</Wrapper>
);

export default Tournament;
